@use 'functions' as f;

@mixin point($point) {
  @media screen and (min-width: f.breakpoint($point)) {
    @content;
  }
}

@mixin fontface($name, $file, $weight: normal, $style: normal) {
  @if ($name != 0) {
    @font-face {
      font-family: '#{$name}';
      src: url('../assets/fonts/#{$file}.woff2') format('woff2'),
        url('../assets/fonts/#{$file}.woff') format('woff2'),
        url('../assets/fonts/#{$file}.ttf') format('truetype');
      font-weight: $weight;
      font-style: $style;
    }
  } @else {
    @content;
  }
}
